.App {
  display: flex;
  justify-content: center;
}

.label {
  font-weight: bold;
  display: flex;
  margin-bottom: 0.5em;
}

/* input[type="radio"] {
  margin-right: 1em;
} */

/* input + label {
  font-weight: 400;
  display: inline-flex;
  margin-right: 2em;
} */


.num {
  width: 91vw
}

.date {
  display: block;
  padding: 18.5px 14px;
  font-size: 1em;
  line-height: 1.42857143;
  color: #555390;
  background-color: #fff;
  background-image: none;
  border: 1px solid #ccc;
  border-radius: 4px;
}

input[type='text'],
input[type='number'],
input[type='email'] {
  width: 76vw;
}

.form-control {
  margin-bottom: 20px;
  font-size: 1em;
  line-height: 1.5;
  letter-spacing: 0.00938em;
  font-family: "Roboto", "Helvetica", "Arial", sans-serif;
}

.error {
  color: #f44336;
  font-size: 0.75em;
  margin-left: 14px;
}
.rest {
  width: 92vw;
}

.Radio {
  display: inline-flex;
  align-items: center;
  margin-right: 0.5em;
  font-size: 16px;
}

.Radio-Input {
  margin: 0 0.5rem 0;
}